<template>
    <div class="setting-wrapper">
        <ul class="extension-list" v-if="!loading">
            <h3 class="title">{{i18n.commonExten}} <i class="iconfont  icon star">&#xe629;</i>({{commonlyExtension.length}})</h3>
            <p class="tips">{{i18n.extenTips}}</p>
            <li class="extension-item" v-for="item in extensions" @click.stop="toggleItem(item)" v-show="!isHide(item.id)">
                <img class="extension-icon" :src="iconSrc(item)">
                <span class="item-name">{{item.name}}</span>
                <span class="enabled" v-show="item.enabled"></span>
            </li>
        </ul>
        <ul class="extension-list" v-if="!loading">
            <h3 class="title">{{i18n.notCommonExten}}<i class="iconfont icon">&#xe6f1;</i></h3>
            <li class="extension-item" v-for="item in extensions" @click.stop="toggleItem(item)" v-show="isHide(item.id)">
                <img class="extension-icon" :src="iconSrc(item)" :class="{'disabled':isHide(item.id)}">
                <span class="item-name">{{item.name}}</span>
                <span class="enabled" v-show="item.enabled"></span>
            </li>
        </ul>
    </div>
</template>
<script>
export default {
    name: 'setting',
    props: {
        extensions: Array,
        commonlyExtension: Array,
        loading: Boolean
    },
    data() {
        return {
            i18n: {
                commonExten: '',
                notCommonExten: '',
                extenTips: ''
            }
        }
    },
    created() {
        this.i18n.commonExten = chrome.i18n.getMessage("commonExten");
        this.i18n.notCommonExten = chrome.i18n.getMessage("notCommonExten");
        this.i18n.extenTips = chrome.i18n.getMessage("extenTips");
    },
    methods: {
        iconSrc: function(item) {
            let result = '';
            if (item.icons && item.icons.length > 0) {
                let length = item.icons.length;
                result = item.icons[length - 1].url;
            }
            return result
        },
        isHide(id) {
            // console.log(this.commonlyExtension)
            return this.commonlyExtension.indexOf(id) > -1 ? false : true;
        },
        toggleItem(item) {
            let index = this.commonlyExtension.indexOf(item.id);
            // console.log(item)
            if (index > -1) {
                this.commonlyExtension.splice(index, 1)
            } else {
                this.commonlyExtension.push(item.id)
            }
            console.log(this.commonlyExtension)
            chrome.storage.sync.set({ 'commonlyExtension': this.commonlyExtension }, () => {
                console.log('Settings saved');
                this.$emit('updateStatus');
            });
        }
    },
    watch: {
        loading: function(newVal) {
            console.log(newVal)
            if (!newVal) {
                this.loading = newVal;
            }
        }
    }
}
</script>
<style lang="scss" scoped>
.setting-wrapper {
    background: #D6D7DE;
    padding: 0 15px 12px 15px;
    .extension-list {
        list-style-type: none;
        padding: 0;
        margin: 0;
        text-align: left;
        .title {
            padding: 4px 8px 4px 0;
            margin: 0;
            font-size: 16px;
            .icon {
                margin: 0px 3px;
                cursor: pointer;
                color: #555;
                font-weight: 500;
                display: inline-block;
                margin-right: 5px;
                &.star {
                    color: #e06363;
                }
            }
        }
        .tips {
            margin: 0;
            padding-left: 4px;
            text-align: left;
            font-size: 14px;
        }
        .extension-item {
            position: relative;
            width: 55px;
            padding: 8px;
            text-align: center;
            box-sizing: border-box;
            cursor: pointer;
            position: relative;
            transition: all 0.5s;
            &:hover {
                .item-name {
                    display: block;
                }
                .extension-icon {
                    transform: scale(1.2);
                    filter: none !important;
                }
            }
            .extension-icon {
                width: 40px;
                background-size: cover;
                transition: all 0.5s;
                &.disabled {
                    filter: grayscale(100%);
                }
            }
            .item-name {
                display: none;
                position: absolute;
                top: 55px;
                left: 50%;
                transform: translateX(-50%);
                overflow: hidden;
                height: 20px;
                line-height: 20px;
                max-width: 150px;
                min-width: 60px;
                background-color: #fff;
                border-radius: 3px;
                padding: 0 3px;
                z-index: 103
            }
            .enabled {
                position: absolute;
                top: 3px;
                right: 3px;
                width: 8px;
                height: 8px;
                border-radius: 4px;
                background: #4BA23C;
                box-shadow: 0 0 5px #7BF444;
            }
        }
    }
}
</style>


