<template>
    <ul class="shortcut-list">
        <li class="list-item" v-for="item in shortcutList" :key="item.name">
            <div class="item " :class="item.class" @click.stop="newTab(item.path)"><i class="iconfont icon" :class="item.icon"></i><span class="title">{{i18n[item.en]}}</span></div>
        </li>
        <!-- <li class="list-item">
            <div class="item download" @click.stop="newTab('chrome://downloads/')"><i class="iconfont icon">&#xe60f;</i><span class="title">{{i18n.downloads}}</span></div>
        </li>
        <li class="list-item">
            <div class="item" @click.stop="newTab('chrome://history/')"><i class="iconfont icon">&#xe7d8;</i><span class="title">{{i18n.history}}</span></div>
        </li>
        <li class="list-item">
            <div class="item" @click.stop="newTab('chrome://extensions/')"><i class="iconfont icon">&#xe641;</i><span class="title">{{i18n.extensions}}</span></div>
        </li>
        <li class="list-item">
            <div class="item" @click.stop="newTab('chrome://settings/')"><i class="iconfont icon">&#xe63b;</i><span class="title">{{i18n.settings}}</span></div>
        </li>
        <li class="list-item">
            <div class="item remove" @click.stop="newTab('chrome://settings/clearBrowserData')"><i class="iconfont icon">&#xe642;</i><span class="title">{{i18n.clearBrowserData}}</span></div>
        </li>
        <li class="list-item">
            <div class="item shop" @click.stop="newTab('https://chrome.google.com/webstore/category/extensions')"><i class="iconfont icon">&#xe62f;</i><span class="title">{{i18n.webstore}}</span></div>
        </li> -->
    </ul>
</template>
<script>
import { shortcutList, bookmarksId } from '../config/index.js'
export default {
    name: 'shortcut',
    data() {
        return {
            shortcutList: [],
            i18n: {
                downloads: '',
                history: '',
                extensions: '',
                settings: '',
                clearBrowserData: '',
                webstore: '',
                bookmarks: ''
            }
        }
    },
    props: {
        extensions: Array
    },
    created() {
        this.shortcutList = shortcutList;
        this.i18n.downloads = chrome.i18n.getMessage("downloads");
        this.i18n.history = chrome.i18n.getMessage("history");
        this.i18n.extensions = chrome.i18n.getMessage("extensions");
        this.i18n.settings = chrome.i18n.getMessage("settings");
        this.i18n.clearBrowserData = chrome.i18n.getMessage("clearBrowserData");
        this.i18n.webstore = chrome.i18n.getMessage("webstore");
        this.i18n.bookmarks = chrome.i18n.getMessage("bookmarks");
    },
    methods: {
        newTab(url) {
            if (!url) {
                return;
            }
            if (url === 'bookmarks') {
                console.log(this.extensions)
                let result = this.extensions.some(item => {
                    return item.id === bookmarksId;
                })
                let storeUrl = "https://chrome.google.com/webstore/detail/kimcgbcdngdnggfmkjdmmjceijnkcjmd"
                let bookmarksUrl = `chrome-extension://${bookmarksId}/index.html`
                url = result ? bookmarksUrl : storeUrl;
            }
            let newTab = {
                url: url,
                active: true
            }
            chrome.tabs.create(newTab, () => {})
        }
    }
}
</script>
<style lang="scss" scoped>
.shortcut-list {
    margin-bottom: 0;
    margin-top: 0;
    overflow: hidden;
    padding: 15px;
    .list-item {
        display: block;
        text-align: center;
        cursor: pointer;
        overflow: hidden;
        float: left;
        width: 25%;
        .item {
            margin: 8px;
            padding: 8px;
            border-radius: 8px;
            text-align: center;
            background: #fff;
            box-shadow: 3px 3px 6px #3b3a49;
            .icon {
                color: #F98168;
                display: block;
                height: 40px;
                line-height: 40px;
                font-size: 32px;
                vertical-align: middle;
            }
            .title {
                display: block;
                margin-top: 8px;
                height: 24px;
                line-height: 24px;
                color: #42b983;
            }
            &.remove {
                .icon {
                    font-size: 44px;
                    padding-top: 4px;
                    box-sizing: border-box;
                }
                .title {
                    width: 72px;
                }
            }
            &.download {
                .icon {
                    font-size: 28px;
                }
            }
            &.shop {
                .icon {
                    font-size: 28px;
                }
            }
            &:hover {
                background: #e5f0fb;
            }
        }
    }
}
</style>


