<template>
    <div class="header-wrapper">
        <div class="header-content">
            <span class="tabs" :class="{'active':showMenu}">
                <i class="iconfont icon extension" :class="{'active':currentActive===1}" @click.stop="setTab(1)">&#xe62b;</i>
                <i class="iconfont icon link" :class="{'active':currentActive===2}" @click.stop="setTab(2)">&#xe612;</i>
                <i class="iconfont icon setting" :class="{'active':currentActive===3}" @click.stop="setTab(3)">&#xe63b;</i>
                </span>
            <div class="menu-plus" @click.stop="toggleMenu()">
                <div class="menu-icon" :class="{'active':showMenu}">
                    <i class="iconfont icon-plus" :class="{'active':showMenu}">&#xe6cc;</i>
                </div>
                <ul class="menu-items">
                    <li class="menu-item itme-left">
                        <span class="item-btn" :class="{'active':showMenu}" @click.stop="toggleDisable(1)">
                        <i class="iconfont close-all">&#xe65f;</i>
                          <span class="left-tips">
                              <span class="tips-text">
                                  {{i18n.closeAllExten}}
                              </span>
                        </span>
                        </span>
                    </li>
                    <li class="menu-item itme-center">
                        <span class="item-btn" :class="{'active':showMenu}" @click.stop="toggleDisable(2)">
                        <i class="iconfont part">&#xe816;</i>
                         <span class="center-tips">
                          <span class="tips-text">{{i18n.openCommonExten}}
                          </span></span>
                        </span>
                    </li>
                    <li class="menu-item itme-right">
                        <span class="item-btn" :class="{'active':showMenu}" @click.stop="toggleDisable(3)">
                        <i class="iconfont open-all">&#xe655;</i>
                         <span class="right-tips">{{i18n.openAllExten}}</span>
                        </span>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>
<script>

export default {
    name: 'header',
    data() {
        return {
            showMenu: false,
            i18n: {
                closeAllExten: '',
                openAllExten: '',
                openCommonExten: ''
            }
        }
    },
    props: {
        currentActive: Number
    },
    created() {
        this.i18n.closeAllExten = chrome.i18n.getMessage("closeAllExten");
        this.i18n.openAllExten = chrome.i18n.getMessage("openAllExten");
        this.i18n.openCommonExten = chrome.i18n.getMessage("openCommonExten");
    },
    methods: {
        setTab(tab) {
            this.$emit('updateActive', tab);
        },
        toggleDisable(type) {
            this.$emit('updateDisabled', type);
        },
        toggleMenu() {
            this.showMenu = !this.showMenu;
            this.$emit('showMenu', this.showMenu);
        }
    }
}
</script>
<style lang="scss" scoped>
.header-wrapper {
    display: flex;
    width: 100%;
    position: relative;
    .header-content {
        position: absolute;
        z-index: 100;
        top: 0;
        left: 0;
        display: flex;
        width: 100%;
        text-align: left;
        .tabs {
            display: inline-block;
            flex: 1;
            position: relative;
            overflow: hidden;
            opacity: 1;
            &.active {
                opacity: 0;
            }
            .icon {
                position: absolute;
                top: 0;
                font-size: 24px;
                cursor: pointer;
                width: 30px;
                height: 58px;
                text-align: center;
                color: #fff;
                &.extension {
                    left: 0;
                }
                &.link {
                    left: 45px;
                }
                &.setting {
                    left: 90px;
                }
                &.active {
                    border-bottom: 2px solid #64F8E4;
                }
            }
        }
        .menu-plus {
            width: 60px;
            height: 60px;
            position: relative;
            .menu-icon {
                position: absolute;
                top: 10px;
                left: 10px;
                cursor: pointer;
                z-index: 10;
                width: 40px;
                height: 40px;
                -webkit-box-align: center;
                -ms-flex-align: center;
                align-items: center;
                text-align: center;
                line-height: 40px;
                font-size: 14px;
                border-radius: 50%;
                background-color: #007fff;
                color: #fff;
                -webkit-user-select: none;
                -moz-user-select: none;
                -ms-user-select: none;
                user-select: none;
                box-shadow: 0 4px 10px rgba(0, 0, 0, .15);
                transition: all 0.5s;
                &.active {
                    background-color: #FF5722;
                }
                .icon-plus {
                    width: 40px;
                    height: 40px;
                    font-size: 28px;
                    display: inline-block;
                    transition: all 0.5s;
                    &.active {
                        transform: rotate(135deg);
                        font-size: 32px;
                        padding: 2px;
                    }
                }
            }
            .menu-items {
                list-style-type: none;
                padding: 0;
                margin: 0;
                position: absolute;
                left: 30px;
                top: 30px;
                .menu-item {
                    position: absolute;
                    top: 0px;
                    left: 0px;
                    width: 0;
                    height: 0;
                    color: #fff;
                    .item-btn {
                        display: block;
                        width: 40px;
                        height: 40px;
                        text-align: center;
                        line-height: 40px;
                        margin-left: -20px;
                        margin-top: -20px;
                        position: absolute;
                        top: 0;
                        left: 0;
                        color: #AD4C4C;
                        background: #4f4f64;
                        border-radius: 50%;
                        border: none;
                        outline: none;
                        transition: all 0.5s;
                        cursor: pointer;
                        &.active {
                            transform: translate3d(0px, 80px, 0px);
                        }
                        .close-all {
                            display: inline-block;
                            font-size: 24px;
                            transform: rotate(-90deg);
                        }
                        .left-tips {
                            display: none;
                            position: absolute;
                            bottom: -5px;
                            left: 10px;
                            transform: rotate(-90deg);
                            .tips-text {
                                position: absolute;
                                top: 0;
                                right: 0;
                                min-width: 80px;
                                background: hsla(0, 0%, 100%, .4);
                                height: 20px;
                                padding: 0 3px;
                                margin: 0;
                                line-height: 20px;
                                border-radius: 3px;
                                white-space: nowrap
                            }
                        }
                        .center-tips {
                            display: none;
                            position: absolute;
                            top: 30px;
                            right: 45px;
                            transform: rotate(-45deg);
                            .tips-text {
                                position: absolute;
                                top: 0;
                                right: 0;
                                min-width: 80px;
                                background: hsla(0, 0%, 100%, .4);
                                height: 20px;
                                padding: 0 3px;
                                margin: 0;
                                line-height: 20px;
                                border-radius: 3px;
                                white-space: nowrap;
                            }
                        }
                        .right-tips {
                            display: none;
                            position: absolute;
                            bottom: -15px;
                            right: 40px;
                            min-width: 80px;
                            background: hsla(0, 0%, 100%, .4);
                            height: 20px;
                            padding: 0 3px;
                            margin: 0;
                            line-height: 20px;
                            border-radius: 3px;
                            white-space: nowrap;
                        }
                        .part {
                            display: inline-block;
                            font-size: 24px;
                            transform: rotate(-45deg);
                        }
                        .open-all {
                            display: inline-block;
                            font-size: 24px;
                            transform: rotate(0deg);
                        }
                    }
                    &.itme-left {
                        transform: rotate(90deg);
                        &:hover {
                            .left-tips {
                                display: block;
                            }
                        }
                    }
                    &.itme-center {
                        transform: rotate(45deg);
                        &:hover {
                            .center-tips {
                                display: block;
                            }
                        }
                    }
                    &.itme-right {
                        transform: rotate(0deg);
                        &:hover {
                            .right-tips {
                                display: block;
                            }
                        }
                    }
                }
            }
        }
    }
}
</style>


