<template>
    <div class="search-wrapper">
        <div class="search-content">
            <span class="search-input">
                    <input  class="search" type="text" v-model="searchItem" v-bind:placeholder="i18n.searchPlaceholder"  v-autofocus="autofocus">
                    <i class="iconfont clear-icon" v-if="searchItem.length>0" @click.stop="cleanSearch">&#xe609;</i>
                     <i class="iconfont serach-icon" >&#xe60d;</i></span>
        </div>
    </div>
</template>
<script>
export default {
    name: 'search',
    data() {
        return {
            searchItem: '',
            i18n: {
                searchPlaceholder: ''
            }
        }
    },
    props: {
        isClear: Boolean
    },
    created() {
        this.i18n.searchPlaceholder = chrome.i18n.getMessage("searchPlaceholder");
    },
    directives: {
        autofocus: {
            inserted: function(el) {
                el.focus();
            },
            update: function(el) {
                el.focus();
            }
        }
    },
    methods: {
        cleanSearch() {
            this.searchItem = '';
        }
    },
    watch: {
        searchItem: function(newVal) {
            this.$emit('updateSearch', this.searchItem)
        }
    }
}
</script>
<style lang="scss" scoped>
.search-wrapper {
    top: 0;
    left: 0;
    width: 100%;
    text-align: left;
    background: #d6d7de;
    .search-content {
        padding: 0 15px;
        .search-input {
            position: relative;
            .search {
                height: 24px;
                line-height: 24px;
                width: 342px;
                border: none;
                outline: none;
                padding: 3px 20px;
                border-radius: 6px;
            }
            .serach-icon {
                position: absolute;
                left: 0;
                top: 0;
                color: #333;
                padding-left: 3px;
                cursor: pointer;
            }
            .clear-icon {
                position: absolute;
                right: 0;
                top: 0;
                color: #333;
                padding-right: 3px;
                cursor: pointer;
            }
        }
    }
}
</style>


