<template>
    <div class="wrapper">
        <div class="header" :class="{'active':showMenu}">
            <eheader :currentActive="currentActive" @updateActive="handlerUpdateActive" @updateDisabled="handlerUpdateDisabled" @showMenu="handlerShowMenu"></eheader>
        </div>
        <div class="main-content " :class="{'active':showMenu}">
            <search @updateSearch="updateSearchHandler" :isClear="isClear" v-show="currentActive===1"></search>
            <ul class="tree-list" v-show="currentActive===1">
                <treeitem v-for="item in allExtension" class="folder-item" :commonlyExtension="commonlyExtension" :extendItem="item" :key="item.id" @setEnabled="setEnabledHandler" @updateStatus="updateStatusHandler" v-show="item.isShow"></treeitem>
                <p v-show="noMatch">{{i18n.noMatch}}</p>
            </ul>
            <div class="shortcut-wrapper" v-show="currentActive===2">
                <shortcut :extensions="allExtension"></shortcut>
            </div>
            <div v-show="currentActive===3">
                <setting :extensions="allExtension" @updateStatus="updateStatusHandler" :commonlyExtension="commonlyExtension" :loading="loading"></setting>
            </div>
        </div>
    </div>
</template>
<script>
import eheader from '../components/header.vue'
import treeitem from '../components/treeitem.vue'
import setting from '../components/setting.vue'
import shortcut from '../components/shortcut.vue'
import search from '../components/search.vue'
export default {
    name: 'dashbord',
    data() {
        return {
            allExtension: [],
            selfId: null,
            currentActive: 1,
            showMenu: false,
            commonlyExtension: [],
            loading: false,
            noMatch: false,
            i18n: {
                noMatch: ''
            }
        }
    },
    components: {
        eheader: eheader,
        treeitem: treeitem,
        setting: setting,
        shortcut: shortcut,
        search: search
    },
    created() {
        this.i18n.noMatch = chrome.i18n.getMessage("noMatch");
        this.loading = true;
        try {
            chrome.storage.sync.get('commonlyExtension', (data) => {
                console.log('get storage');
                console.log(data);
                console.log(data.commonlyExtension);
                this.$nextTick(() => {
                    if (data && data.commonlyExtension) {
                        this.commonlyExtension = data.commonlyExtension;
                    } else {
                        this.commonlyExtension = [];
                    }
                    this.loading = false;
                })
            });
        } catch (e) {
            console.log(e);
            this.$nextTick(() => {
                this.commonlyExtension = [];
                this.loading = false;
            })
        }
        this.getSelfInfo();
    },
    methods: {
        getSelfInfo() {
            chrome.management.getSelf((data) => {
                this.selfId = data.id;
                this.loadAllNodes();
            })
        },
        loadAllNodes() {
            this.allExtension.length = 0;
            chrome.management.getAll((data) => {
                let nodes = data.filter((item) => {
                    return item.id !== this.selfId;
                })
                let enabledCount = 0;
                nodes.forEach((item) => {
                    console.log(item);
                    item.isShow = true;
                    if (item.enabled) {
                        enabledCount += 1;
                    }
                })
                nodes = this.sortList(nodes);
                this.allExtension = nodes.sort((x, y) => {
                    return y.sort - x.sort
                });
                if (enabledCount > 0) {
                    this.setBadge(enabledCount);
                } else {
                    this.setBadge('');
                }
                // console.log(enabledCount);
            });
        },
        setEnabledHandler() {
            this.loadAllNodes();
        },
        updateStatusHandler() {
            this.loadAllNodes();
        },
        handlerUpdateActive(val) {
            this.currentActive = val;
        },
        setBadge(count) {
            let num = count.toString();
            chrome.browserAction.setBadgeText({ text: num })
            chrome.browserAction.setBadgeBackgroundColor({ color: "#369A3E" })
        },
        handlerUpdateDisabled(val) {
            if (this.loading) {
                return;
            }
            this.allExtension.forEach((item) => {
                let itemId = item.id;
                let option;
                if (val == 2) {
                    let index = this.commonlyExtension.indexOf(itemId);
                    if (index > -1) {
                        option = true;
                    } else {
                        option = false;
                    }
                } else if (val == 1) {
                    option = false;
                } else if (val == 3) {
                    option = true;
                }
                this.setDisable(itemId, option)
            })
            this.loadAllNodes();
            this.searchItem = '';
        },
        handlerShowMenu(val) {
            this.showMenu = val;
        },
        setDisable(id, val) {
            chrome.management.setEnabled(id, val, (data) => {})
        },
        filterExtension(value) {
            let str = value.toLocaleLowerCase();
            let count = 0;
            let length = this.allExtension.length;
            this.allExtension.forEach((item) => {
                let name = item.name.toLocaleLowerCase()
                // let desc = item.description.toLocaleLowerCase()
                if (name.indexOf(str) == -1) {
                    item.isShow = false;
                    count++;
                } else {
                    item.isShow = true;
                }
            })
            if (count == length) {
                this.noMatch = true;
            } else {
                this.noMatch = false;
            }
            console.log(this.allExtension)
        },
        updateSearchHandler(value) {
            console.log(value)
            console.log(value.length)
            if (value.length == 0) {
                this.loadAllNodes();
            } else {
                this.filterExtension(value)
            }
        },
        sortList(arr) {
            let newArr = arr.map(item => {
                if (item.enabled) {
                    item.sort = 10;
                } else {
                    item.sort = -10;
                }
                if (this.commonlyExtension.indexOf(item.id) > -1) {
                    item.sort = item.sort + 5;
                } else {
                    item.sort = item.sort - 5;
                }
                return item;
            })
            return newArr;
        }
    }
}
</script>
<style lang="scss" scoped>
.wrapper {
    background: #D6D7DE;
    overflow-y: auto;
    &::-webkit-scrollbar {
        width: 8px;
    }
    &::-webkit-scrollbar-track {
        background-color: #20272F;
    }
    &::-webkit-scrollbar-button {
        display: none;
    }
    &::-webkit-scrollbar-thumb {
        border-radius: 5px;
        width: 6px;
        background-color: #486f96;
    }
    &::-webkit-scrollbar-thumb:hover {
        background-color: #486f96;
    }
    &::-webkit-scrollbar-thumb:active {
        background-color: #486f96;
    }
    .header {
        position: fixed;
        top: 0;
        left: 0;
        display: flex;
        height: 60px;
        line-height: 60px;
        padding: 0 10px;
        background: #3B3A49;
        width: 100%;
        box-sizing: border-box;
        z-index: 100;
        box-shadow: 2px 2px 8px #333;
        transition: all 0.5s;
        &.active {
            height: 150px;
        }
    }
    .main-content {
        width: 420px;
        min-height: 60px;
        margin-top: 60px;
        padding: 10px 0;
        background: #D6D7DE;
        box-sizing: border-box;
        overflow: hidden;
        transition: all 0.5s;
        max-height: 540px;
        overflow-y: scroll;
        &::-webkit-scrollbar {
            width: 8px;
        }
        &::-webkit-scrollbar-track {
            background-color: #D4D2D4;
        }
        &::-webkit-scrollbar-button {
            display: none;
        }
        &::-webkit-scrollbar-thumb {
            border-radius: 5px;
            width: 6px;
            background-color: rgba(80, 80, 80, 0.35);
        }
        &.active {
            margin-top: 150px;
        }
        .tree-list {
            height: 100%;
            margin-bottom: 0;
            margin-top: 0;
            overflow-x: hidden;
            overflow-y: auto;
            -webkit-overflow-scrolling: touch;
            &::-webkit-scrollbar {
                width: 12px
            }
            &::-webkit-scrollbar-track {
                background-color: #607d9b;
            }
            &::-webkit-scrollbar-thumb {
                width: 10px;
                border-radius: 5px;
                background-color: #486f96;
            }
        }
    }
}
</style>


